import { Component, OnInit } from '@angular/core';

import { Todo } from './todo';
import { TodoService } from './todo.service';

@Component({
  selector: 'td-todo',
  templateUrl: './todo.component.html',
  styleUrls: ['./todo.component.less']
})
export class TodoComponent implements OnInit {

  items: Todo[] = [];

  constructor(private todoService: TodoService) {
      
  }

  ngOnInit(): void {
    this.getItems();
    window.addEventListener('beforeunload', this.saveItems.bind(this));
  }

  onItemEdit(item: Todo): void {
    if (!item.text) {
      this.deleteItem(item);
    }
  }

  onItemDelete(item: Todo): void {
    this.deleteItem(item);
  }

  onItemAdd(text: string): void {
    let date = new Date();
    this.items.push({
      id: date.getTime(),
      checked: false,
      text: text,
      date: date
    });
  }

  deleteItem(item: Todo): void {
    let index = this.items.indexOf(item);
    if (index !== -1) {
      this.items.splice(index, 1);
    }
  }

  saveItems(): void {
    this.todoService.saveItems(this.items);
  }

  getItems(): void {
    this.todoService.getItems().subscribe(items => this.items = items);
  }

}



